import React from "react";
import { Select } from "antd";

export default function EventTypeSelectBox(props) {
  return (
    <Select {...props}>
      <Select.Option key="JobCanceled" value="JobCanceled">Job Canceled</Select.Option>
      <Select.Option key="JobFailed" value="JobFailed">Job Failed</Select.Option>
      <Select.Option key="JobCompleted" value="JobCompleted">Job Completed</Select.Option>
      <Select.Option key="JobStarted" value="JobStarted">Job Started</Select.Option>
      <Select.Option key="JobFeedbackRequested" value="JobFeedbackRequested">Job Feedback Requested</Select.Option>
      <Select.Option key="ServerStarted" value="ServerStarted">Server Started</Select.Option>
      <Select.Option key="ServerStopped" value="ServerStopped">Server Stopped</Select.Option>
      <Select.Option key="DashboardStarted" value="DashboardStarted">Dashboard Started</Select.Option>
      <Select.Option key="DashboardStopped" value="DashboardStopped">Dashboard Stopped</Select.Option>
      <Select.Option key="UserLogin" value="UserLogin">User Login</Select.Option>
      <Select.Option key="RevokedAppTokenUsage" value="RevokedAppTokenUsage">Revoked App Token Usage</Select.Option>
      <Select.Option key="ProtectEvent" value="ProtectEvent">PowerShell Protect Event</Select.Option>
    </Select>
  );
}
